.cat-info {
  margin-top: 30px;
  display: flex;
}

.cat-img {
  width: 350px;
}

.cat-temperament {
  font-weight: 700;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border-top: 4px solid #fff;
  border-right: 4px solid rgba(0, 0, 0, 0);
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 48px;
  height: 48px;
  border-bottom: 4px solid rgba(0, 0, 0, 0);
  border-left: 4px solid #ff3d00;
  border-radius: 50%;
  animation: rotation .5s linear infinite reverse;
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.error {
  color: red;
  font-weight: 700;
}

.loader, .error {
  display: none;
}

.loader.active, .error.active {
  display: block;
}

